body {
    background: rgb(161, 240, 229);
  }

  .todo-container {
    background: rgb(7, 2, 2);
    width: 50vw;
    margin: 10em auto;
    height: 30em;
    border-radius: 15px;
    padding: 20px 10px;
    color: white;
    border: 3px solid rgb(88, 52, 219);
  }

  .task {
    border: 1px solid white;
    border-radius: 5
    px;
    padding: 0.5em;
    margin: 0.5em;
  }

  .task button{
    background: rgb(6, 77, 158);
    border-radius: 5px;
    margin: 0px 5px;
    padding: 3px 5px;
    border: none;
    cursor: pointer;
    color: white;
    float: right;
  }

  .header {
    margin: 0.5em;
    font-size: 2em;
    text-align: center;
  }

  .create-task input[type=text] {
    margin: 2.5em 2em;
    width: 80%;
    outline: none;
    border: none;
    padding: 0.7em;
  }